import './AuthForm.css';
import Button from '../common/Button';
import { Link } from 'react-router-dom';

const textMap = {
  login: '',
  register: '',
};

const AuthForm = ({ type, form, onChange, onSubmit, error }) => {
  const text = textMap[type];
  return (
    <>
      <div className="AuthFormBlock">
        <div className="logo-area">
          <Link to="/">INSTAGRAM</Link>
        </div>
        {type === 'register' && (
          <h2>    .</h2>
        )}
        <form onSubmit={onSubmit}>
          <input
            className="StyledInput"
            autoComplete="username"
            name="username"
            placeholder=" "
            onChange={onChange}
            value={form.username}
          />
          {type === 'register' && (
            <input
              className="StyledInput"
              autoComplete="uname"
              name="uname"
              placeholder=""
              onChange={onChange}
              value={form.name}
            />
          )}
          <input
            className="StyledInput"
            autoComplete="new-password"
            name="password"
            placeholder=""
            type="password"
            onChange={onChange}
            value={form.password}
          />
          {type === 'register' && (
            <input
              className="StyledInput"
              autoComplete="new-password"
              name="passwordConfirm"
              placeholder=" "
              type="password"
              onChange={onChange}
              value={form.passwordConfirm}
            />
          )}
          {/*    */}
          {error && <div className="ErrorMessage">{error}</div>}

          <Button className="Button">{text}</Button>
        </form>
      </div>

      {/*         */}
      <div className="Switch">
        {type === 'login' ? (
          <h3>
             ?{' '}
            <Link to="/register">
              <span className="blue"></span>
            </Link>
          </h3>
        ) : (
          <h3>
             ?{' '}
            <Link to="/login">
              <span className="blue"></span>
            </Link>
          </h3>
        )}
      </div>
      <div className={'else3'}>
        <span>Meta </span>
        <span> </span>
        <span> </span>
        <span>  </span>
        <span> </span>
        <span>API </span>
        <span> </span>
        <span> </span>
        <span>  </span>
        <span> </span>
        <span> </span>
        <span>Instagram Life </span>
        <span>  &amp; </span>
      </div>
      <div className={'else4'}>
        <span> </span>
        <span> </span>
        <span>   </span>
        <span> </span>
        <span>  </span>
      </div>
      <div className={'else2'}> 2022 Instagram from Meta</div>
    </>
  );
};

export default AuthForm;
