import './storyStyle.css'
import {useEffect, useRef, useState} from "react";

const Story = () => {
    const [members, setMember] = useState([]);

    // json   mock data useEffect fetch   useState 
    useEffect(() => {
        fetch('/data/friend.json', {method: 'GET'})
            .then((res) => (res.json()))
            .then((data) => {
                shuffle(data) //         // shuffle          shuffle 
                setMember(data);
            });
    }, [setMember]); // setMember   useEffect  // shuffle  setMember   members  setMember 


    // - (Fisher-Yates shuffle):          
    function shuffle(array) {
        for (let index = array.length - 1; index > 0; index--) {
            //  index   (0    )
            const randomPosition = Math.floor(Math.random() * (index + 1));

            //    , randomPosition    .
            const temporary = array[index];
            array[index] = array[randomPosition];
            array[randomPosition] = temporary;
            // array.sort(() => Math.random() -0.5);         ,     
        }
    }

    //  
    const carousel = useRef();
    const [nowX, setNowX] = useState(0);

    useEffect(() => {
        carousel.current.style.transform = `translateX(${nowX}vw)`;
    }, [nowX]);

    const prevButton = (e) => {
        e.preventDefault()
        if (nowX === 0) { // nowX 0   
            setNowX(nowX);
        } else {
            setNowX((prop) => prop + 7);
            // console.log(`it's work ${nowX}`); //  
        }
    };

    const nextButton = (e) => {
        e.preventDefault()
        if (nowX === -7) {
             // nowX -7    
        } else {
            setNowX(nowX - 7);
        }
        // console.log(`it's work ${nowX}`); //  
    };



    // console.log("userMember->", userMembers);

    return (
        <div className={'story'}>
            <div className={'storyBox'}>
                <button aria-label={"next"} className={"button button-prev"} onClick={prevButton}>&lt;</button>
                <button aria-label={"next"} className={"button button-next"} onClick={nextButton}>&gt;</button>
                <div className={'storyItemsBox'} ref={carousel}>
                    {/* eslint-disable-next-line array-callback-return */}
                    {members.map((member) => {
                        if (member.storage === true) {
                            return (
                                <div className={'story-item'} key={member.id}>
                                    <div className={'story-img'}>
                                        <img src={member.userImg} alt={'profile'}/>
                                    </div>
                                    <div className={'story-id'}>
                                        {member.userId}
                                    </div>
                                </div>
                            )
                        }
                    })}
                </div>
            </div>
        </div>
    )
};

export default Story;
